// @flow
import React, { memo } from 'react';
import Table from 'commons/components/Table';
import Pagination from 'react-js-pagination';
import LengthChart from 'commons/components/LengthChart';
import TitleSubHeader from 'commons/components/TitleHeader/titleSub';
import LineChart2 from 'commons/components/LineChart/LineChart2';
import IMAGES from 'themes/images';
import { headStatusCompany } from '../constant';

import BoxGroup from '../BoxGroup';
import GroupCompareChart from '../GroupCompareChart';
import GroupActionDownload from '../GroupActionDownload';
import { FilterSearch } from '../FilterSearch';

type Props = {
  rawData: any,
  dataBoxContent: Object,
  handleChangeSearch: Function,
  paramsSearch: Object,
  totalPage: number,
  perPage: number,
  listStatusCompanySelect: Array<{
    id: number,
    value: any,
    label: string,
  }>,
  listInverter: Array<{
    id: number,
    value: any,
    label: string,
  }>,
  tabActive: any,
  dateTime: Object,
  handleSubmitSearch: Function,
  chartData: any,
  id: string,
};

const ItemContentTab = ({
  rawData,
  dataBoxContent,
  handleChangeSearch,
  paramsSearch,
  listStatusCompanySelect,
  listInverter,
  totalPage,
  perPage,
  tabActive,
  dateTime,
  handleSubmitSearch,
  chartData,
  id,
}: Props) => {
  const dataLengthChart = [
    {
      id: 1,
      name: 'PV',
      color: '#8567b4',
    },
    {
      id: 2,
      name: 'PV',
      color: '#c05e13',
    },
    {
      id: 3,
      name: '',
      color: '#fe8224',
    },
    {
      id: 4,
      name: '',
      color: '#ffcc00',
    },
    {
      id: 5,
      name: '',
      color: '#102a82',
    },
  ];
  return (
    <div className="content-wrap-tab">
      <BoxGroup
        dataBoxContent={dataBoxContent}
        paramsSearch={paramsSearch}
        handleChangeSearch={handleChangeSearch}
      />
      <FilterSearch
        listStatusCompanySelect={listStatusCompanySelect}
        listInverter={listInverter}
        handleChangeSearch={handleChangeSearch}
        paramsSearch={paramsSearch}
        handleSubmitSearch={handleSubmitSearch}
        listInverter1={listInverter}
        activeTab={tabActive}
      />

      <div className="group-char" id="groupChart">
        <div className="group-char-left">
          <GroupCompareChart
            paramsSearch={paramsSearch}
            handleChangeSearch={handleChangeSearch}
          />
          <div className="group-length-chart">
            <LengthChart dataLengthChart={dataLengthChart} />
          </div>
        </div>
        <div className="group-char-right">
          {tabActive === id.toString() && chartData && (
            <LineChart2
              dataChart={chartData}
              unitLine1="V"
              unitLine2="A"
              unitLine3="A"
              unitLine4="kW"
              unitLine5="V"
              optionLine={{
                line1: paramsSearch?.PVVoltage,
                line2: paramsSearch?.PVCurrent,
                line3: paramsSearch?.outputVoltage,
                line4: paramsSearch?.outputCurrent,
                line5: paramsSearch?.print,
              }}
              typeLine="line"
              widthLine={2}
              unitLeft="V"
              unitRight="A"
              showPoint1
              showPoint2
              showPoint3
            />
          )}
        </div>
      </div>

      <TitleSubHeader title="  " />
      <GroupActionDownload
        paramsSearch={paramsSearch}
        handleChangeSearch={handleChangeSearch}
        linkDownTable={`operator/statistic/?inverter_id=${tabActive}&pos_id=${paramsSearch?.posSelected}&time_to=${dateTime?.to}&time_from=${dateTime?.from}&type=${paramsSearch?.classification}`}
      />
      <div>
        <Table
          tableHeads={headStatusCompany}
          tableBody={rawData}
          // isShowId
        />
        <div className="opacity d-block pagination mt-0">
          {totalPage > perPage && (
            <div className="wrapper-device__pagination mt-0">
              <Pagination
                activePage={paramsSearch?.page}
                itemsCountPerPage={perPage}
                totalItemsCount={totalPage}
                pageRangeDisplayed={5}
                onChange={(e) => handleChangeSearch(e, 'page')}
                itemClass="page-item"
                linkClass="page-link"
                firstPageText={
                  <img
                    src={IMAGES.double_arrow_left}
                    alt=""
                    className="double-prev"
                  />
                }
                lastPageText={
                  <img
                    src={IMAGES.double_arrow_right}
                    alt=""
                    className="double-prev"
                  />
                }
                prevPageText={
                  <img src={IMAGES.arrow_left} alt="" className="double-prev" />
                }
                nextPageText={
                  <img
                    src={IMAGES.arrow_right1}
                    alt=""
                    className="double-prev"
                  />
                }
              />
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default memo<Props>(ItemContentTab);
